import React, { useContext } from "react";
import "./App.css";
import fire from "./img/38 1.png";
import { useNavigate } from "react-router-dom";
import { stateContext } from "./Context/Context";

let newArray = [];

const App = () => {
  const { state, dispatch } = useContext(stateContext);
  const Navigate = useNavigate();

  const levelObject = [
    {
      level: "level 1",
      imgSide: "H-img",
      textSide: "H-text",
      lineSide: "H-line",
    },
    {
      level: "level 2",
      imgSide: "H-img-1",
      textSide: "H-text-1",
      lineSide: "H-line-1",
    },
    {
      level: "level 3",
      imgSide: "H-img",
      textSide: "H-text",
      lineSide: "H-line",
    },
    {
      level: "level 4",
      imgSide: "H-img-1",
      textSide: "H-text-1",
      lineSide: "H-line-1",
    },
    {
      level: "level 5",
      imgSide: "H-img",
      textSide: "H-text",
      lineSide: "H-line-2",
    },
  ];

  newArray.push(state.countNumber)
  console.log(newArray);

  const slidePage = (x) => {
    console.log(state.countNumber);
    if (x == state.countNumber) {
      Navigate("/sliders");
    }
  };

  console.log(state.countNumber);

  return (
    <>
      <div className="H-container">
        <div className="H-row">
          {levelObject.map((e, i) => (
            <div className="H-col" key={i}>
              <div className="H-box">
                {e.imgSide === "H-img" ? (
                  <div className="H-img-text">
                    <div className={e.imgSide} onClick={() => slidePage(i)}>
                      <div
                        className="img"
                        style={{
                          filter:
                            newArray.includes(i + 1)
                              ? "grayscale(0)"
                              : "grascale(1)",
                        }}
                      >
                        <img src={fire} alt="no image" />
                      </div>
                    </div>
                    <div className={e.textSide}>
                      <h5
                        style={{
                          filter:
                            newArray.includes(i + 1)
                              ? "opacity(1)"
                              : "opacity(0.3)",
                        }}
                      >
                        {e.level}
                      </h5>
                    </div>
                  </div>
                ) : (
                  <div
                    className={e.lineSide}
                    style={{
                      filter:
                        newArray.includes(i + 1)
                          ? "opacity(1)"
                          : "opacity(0.3)",
                    }}
                  >
                    <div className="border doted">
                      <div className="dot"></div>
                    </div>
                  </div>
                )}
                {e.lineSide === "H-line" ? (
                  <div
                    className={e.lineSide}
                    style={{
                      filter:
                        newArray.includes(i + 1)
                          ? "opacity(1)"
                          : "opacity(0.3)",
                    }}
                  >
                    <div className="border doted">
                      <div className="dot"></div>
                    </div>
                  </div>
                ) : e.lineSide === "H-line-2" ? (
                  <div
                    className={e.lineSide}
                  >
                    <div className="border doted">
                      <div className="dot"></div>
                    </div>
                  </div>
                ) : (
                  <div className="H-img-text">
                    <div className={e.imgSide} onClick={() => slidePage(i)}>
                      <div
                        className="img"
                        style={{
                          filter:
                            newArray.includes(i + 1)
                              ? "grayscale(0)"
                              : "grascale(1)",
                        }}
                      >
                        <img src={fire} alt="no image" />
                      </div>
                    </div>
                    <div className={e.textSide}>
                      <h5
                        style={{
                          filter:
                            newArray.includes(i + 1)
                              ? "opacity(1)"
                              : "opacity(0.3)",
                        }}
                      >
                        {e.level}
                      </h5>
                    </div>
                  </div>
                )}
              </div>
            </div>
          ))}
        </div>
      </div>
    </>
  );
};

export default App;
