import React, { useContext, useState } from "react";
import "./App.css";
import { useNavigate } from "react-router-dom";
import {
  Box,
  Button,
  IconButton,
  Menu,
  MenuItem,
  Tooltip,
} from "@mui/material";
import MenuIcon from "@mui/icons-material/Menu";
import CloseIcon from "@mui/icons-material/Close";
import slide1 from "./img/Rectangle 7.png";
import slide2 from "./img/next slide.jpg";
import slide3 from "./img/GAME OVER.jpg";
import { stateContext } from "./Context/Context";
import Dialog from "@mui/material/Dialog";
import DialogActions from "@mui/material/DialogActions";
import DialogContent from "@mui/material/DialogContent";
import DialogTitle from "@mui/material/DialogTitle";
import success from "./img/animation_lk4dum6e_small.gif";

const Slider = () => {
  const { state, dispatch } = useContext(stateContext);
  const [anchorElUser, setAnchorElUser] = useState(null);
  const [currentSlide, setCurrentSlide] = useState(state.SliderIndex);
  const Navigate = useNavigate();

  console.log(state.SliderIndex);

  const slides = [
    {
      image: slide1,
      alt: "Slide 1",
    },
    {
      image: slide2,
      alt: "Slide 2",
    },
    {
      image: slide3,
      alt: "Slide 3",
    },
  ];

  const handleOpenUserMenu = (event) => {
    setAnchorElUser(event.currentTarget);
  };

  const handleCloseUserMenu = () => {
    setAnchorElUser(null);
  };

  const Home = () => {
    Navigate("/home");
    console.log(currentSlide);
    dispatch({ type: "S-Index", payload: currentSlide });
    localStorage.setItem("slideIndex", currentSlide);
  };

  const handleBack = () => {
    setCurrentSlide((prevSlide) =>
      prevSlide === 0 ? slides.length - 1 : prevSlide - 1
    );
    console.log(currentSlide);
  };

  const handleNext = () => {
    setCurrentSlide((prevSlide) =>
      prevSlide === slides.length - 1 ? 0 : prevSlide + 1
    );
  };

  const finish = () => {
    Navigate("/home");
    dispatch({ type: "S-Index", payload: 0 });
    localStorage.setItem("slideIndex", 0);

    let count = localStorage.getItem("count");
    ++count;
    localStorage.setItem("count", count);
    dispatch({ type: "count", payload: count });
  };

  const [open, setOpen] = useState(false);

  const handleClickOpen = () => {
    setOpen(true);
  };

  const handleClose = () => {
    setOpen(false);
    let count = localStorage.getItem("count");
    ++count;
    localStorage.setItem("count", count);
    dispatch({ type: "count", payload: count });
    Navigate("/home");
  };

  const { image, alt } = slides[currentSlide];

  return (
    <div className="S-container">
      <div className="S-menu">
        <Box sx={{ flexGrow: 0 }} className="menu-box">
          <Tooltip title="Open settings">
            <IconButton onClick={handleOpenUserMenu} sx={{ p: 0 }}>
              <MenuIcon className="icon" />
            </IconButton>
          </Tooltip>
          <Menu
            sx={{ mt: "45px" }}
            id="menu-appbar"
            anchorEl={anchorElUser}
            anchorOrigin={{
              vertical: "top",
              horizontal: "right",
            }}
            keepMounted
            transformOrigin={{
              vertical: "top",
              horizontal: "right",
            }}
            open={Boolean(anchorElUser)}
            onClose={handleCloseUserMenu}
          >
            <MenuItem onClick={handleCloseUserMenu}>
              <p onClick={Home}>
                close <CloseIcon className="closeIcon" />
              </p>
            </MenuItem>
          </Menu>
        </Box>
      </div>
      <div className="S-slide">
        <img src={image} alt={alt} />
      </div>
      <div className="S-pre-nex">
        <Button
          variant="contained"
          onClick={handleBack}
          disabled={currentSlide === 0}
        >
          Back
        </Button>
        {state.countNumber == 4 && currentSlide == slides.length - 1 ? (
          <div>
            <Button variant="outlined" onClick={handleClickOpen}>
              Finish
            </Button>
            <Dialog
              open={open}
              onClose={handleClose}
              aria-labelledby="alert-dialog-title"
              aria-describedby="alert-dialog-description"
            >
              <DialogTitle id="alert-dialog-title">
                {"Success 5 Level Complete"}
              </DialogTitle>
              <DialogContent>
                <img src={success} alt="Success" />
              </DialogContent>
              <DialogActions>
                <Button onClick={handleClose} autoFocus>
                  Done
                </Button>
              </DialogActions>
            </Dialog>
          </div>
        ) : currentSlide === slides.length - 1 ? (
          <Button variant="contained" onClick={finish}>
            Finish
          </Button>
        ) : (
          <Button variant="contained" onClick={handleNext}>
            Next
          </Button>
        )}
      </div>
    </div>
  );
};

export default Slider;
