export const initialState = {
  SliderIndex: JSON.parse(localStorage.getItem("slideIndex")) || 0,
  countNumber: JSON.parse(localStorage.getItem("count")) || 0,
  successFile: 0,
};

export const stateReducer = (state, action) => {
  console.log("State :", state, "Action :", action);

  switch (action.type) {
    case "S-Index":
      return {
        ...state,
        SliderIndex: action.payload,
      };
    case "count":
      return {
        ...state,
        countNumber: action.payload,
      };
    case "success":
      return {
        ...state,
        successFile: action.payload,
      }
    default:
      return state;
  }
};
